<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            Quote
                        </h5>
                        <button v-on:click="ClosePOP($event)" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="listing"> 
                            <tg-list 
                                :showCheckBox="true" 
                                :IsShowAction="false" 
                                :listType="['List']" 
                                ModuleName="Asset"
                                SubModuleCode="Asset"
                                IdentityColumn="quoteId"                                                    
                                :HeaderText="$t('AssetManagement')"
                                :widgets="widgets"
                                :ListData="NewRequisitionList" 
                                :HeaderData="Headers"                       
                                :callbackfunction="FeatchData"                                
                                :LegendArray="LegendArray"
                                :SortExp="SortExp"
                                :SortBy="SortBy"
                                :NorecordfoundText="$t('NorecordfoundText')"
                                
                                :TotalRecords="TotalRecords"                                
                                :RenderRowActionMethod="renderActions" 
                                :ListDataCallBackFunction="FeatchData"
                                v-on:PagerButtonClick="pagerMethod" 
                                v-on:HeaderButtonClick="commonHeaderButtonClick">

                                <template v-slot:slotdata="{ data }">
                                    <!-- <template v-if="data.column.COLUMN_NAME == 'CreatedAt'">
                                        <span>{{ $options.filters.formatDateTime(data.row.CreatedAt) }}</span>
                                    </template> -->
                                    <template v-if="data.column.COLUMN_NAME == 'StatusName'">
                                            <em class="active-disabled" v-if="data.row.status_id==1001">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-success">{{data.row.StatusName}}</span>
                                            </em>                                                
                                            <em class="active-disabled" v-else-if="data.row.StatusName=='Approved'">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-warning">{{data.row.StatusName}}</span>
                                            </em>                                    
                                            <em class="active-disabled" v-else-if="data.row.StatusName=='Rejected'">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-warning">{{data.row.StatusName}}</span>
                                            </em>      
                                                                                            
                                            <em v-else class="inactive-disabled">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-danger">{{data.row.StatusName}}</span>
                                            </em>
                                    </template>
                                </template>

                        </tg-list>
                        <!-- :HeaderButtons="listheaderbuttons"  -->
                            
                            </div>
                        </div>                        
                    </div> 
                    <!-- <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            OnlinePurchase
                        </h5>
                        <button v-on:click="ClosePOP($event)" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="listing"> 
                            <tg-list 
                                :showCheckBox="true" 
                                :IsShowAction="false" 
                                :listType="['List']" 
                                ModuleName="Asset"
                                SubModuleCode="Asset"
                                IdentityColumn="quoteId"                                                    
                                :HeaderText="$t('AssetManagement')"
                                :widgets="widgets"
                                :ListData="PurchaseNewRequisitionList" 
                                :HeaderData="Headeres"                       
                                :callbackfunction="FeatchData"                                
                                :LegendArray="LegendArray"
                                :SortExp="SortExp"
                                :SortBy="SortBy"
                                :NorecordfoundText="$t('NorecordfoundText')"
                                
                                :TotalRecords="TotalRecords"                                
                                :RenderRowActionMethod="renderActions" 
                                :ListDataCallBackFunction="FeatchData"
                                v-on:PagerButtonClick="pagerMethod" 
                                v-on:HeaderButtonClick="commonHeaderButtonClick">

                                <template v-slot:slotdata="{ data }">                                    
                                    <template v-if="data.column.COLUMN_NAME == 'StatusName'">
                                            <em class="active-disabled" v-if="data.row.status_id==1001">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-success">{{data.row.StatusName}}</span>
                                            </em>                                                
                                            <em class="active-disabled" v-else-if="data.row.StatusName=='Approved'">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-warning">{{data.row.StatusName}}</span>
                                            </em>                                    
                                             <em class="active-disabled" v-else-if="data.row.StatusName=='Rejected'">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-warning">{{data.row.StatusName}}</span>
                                            </em>                                                   
                                            <em v-else class="inactive-disabled">
                                                <span class="btn d-inline-block w-80 text-center text-truncate btn-danger">{{data.row.StatusName}}</span>
                                            </em>
                                    </template>
                                </template>
                        </tg-list>
                        
                            
                            </div>
                        </div>                        
                    </div>                   -->
                                     
                </div>
            </div>
        </div>
   
    
</template>
    
    <script>
    import DataService from '../../service/DataService'
    export default{
       props:{
        RequisitionData:{
                type:Array,
                required:true,
            }    
       },
        data(){
            return{
                NewRequisitionList:[],
                PurchaseNewRequisitionList:[],
                noRecord:false,
                isLoading:false,                
                Headers: [
                    {
                    "COLUMN_NAME": "vendor",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Vendor"),
                    "DISPLAY_ORDER": 1,
                    },
                    {
                    "COLUMN_NAME": "quantity",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Quantity"),
                    "DISPLAY_ORDER": 2,
                    },
                    {
                    "COLUMN_NAME": "price",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("UnitPrice"),
                    "DISPLAY_ORDER": 3,
                    },
                    {
                    "COLUMN_NAME": "total_Price",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("TotalPrice"),
                    "DISPLAY_ORDER": 4,
                    },
                    {
                    "COLUMN_NAME": "quote",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Quote"),
                    "DISPLAY_ORDER": 5,
                    },
                    {
                    "COLUMN_NAME": "StatusName",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Status"),
                    "DISPLAY_ORDER": 6,
                        settings: {
                            isInSlot: true
                        }
                    },
                ],
                Headeres: [
                    {
                    "COLUMN_NAME": "vendor",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Vendor"),
                    "DISPLAY_ORDER": 1,
                    },
                    {
                    "COLUMN_NAME": "quantity",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Quantity"),
                    "DISPLAY_ORDER": 2,
                    },
                    {
                    "COLUMN_NAME": "price",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("UnitPrice"),
                    "DISPLAY_ORDER": 3,
                    },
                    {
                    "COLUMN_NAME": "total_Price",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("TotalPrice"),
                    "DISPLAY_ORDER": 4,
                    },
                    {
                    "COLUMN_NAME": "purchaseLink",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Link"),
                    "DISPLAY_ORDER": 5,
                    },
                    {
                    "COLUMN_NAME": "StatusName",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Status"),
                    "DISPLAY_ORDER": 6,
                         settings: {
                           isInSlot: true
                        }
                    },
                ],
                listheaderbuttons:[],
                LegendArray:[],            
                widgets:[],            
                noRecord:true,
                TotalRecords:0,
                CurrentPage: 1,
                TotalPages: 0,      
                SortBy: '',
                SortExp: '',
                PageNumber: 1,
                isPaged: 1,
                PageSize: 10,
                }
        },
        created:function(){                           
            this.FeatchData();
        },
        methods:{            
            FeatchData() {                
                var vm = this;
                    vm.isLoading = true;
                    var params = `requisitionId=${vm.RequisitionData.AssetRequisitionId}&type=Quote`;
                    DataService.ViewRequisitionDetail(params).then(response => {                       
                        if (response.data.QuoteNewRequisitionList != null) {                                         
                            if (response.data.QuoteNewRequisitionList.length > 0) {
                                vm.NewRequisitionList = response.data.QuoteNewRequisitionList;
                                vm.PurchaseNewRequisitionList = response.data.PurchaseNewRequisitionList;
                                // vm.TotalRecord = vm.PendingRequestData[0].TOTAL_PAGES;
                                // vm.TotalPage = vm.TotalRecord;
                                // vm.CurrentPage = vm.PageNumber;
                                vm.noRecord = false;                        
                                vm.isLoading = false;
                            }
                            else {
                                vm.NewRequisitionList=[];
                                vm.noRecord = true;                        
                                vm.isLoading = false;
                            }
                        }
                        setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                    });
                    vm.isLoading = false;
            },               
            onCancel:function(item){
                this.ClosePOP(item);
            },
            ClosePOP:function(item){                
                this.$emit('ClosePOPuP',item);
            },
            pagerMethod: function (value) {           
                this.pageSize = value.pageSize;
                this.PageNumber = value.PageNumber;
                this.FeatchData();
            },
            commonHeaderButtonClick:function(){                
            },
            renderActions() {                
            },
        }
    }
    </script>